<template>
    <div :style="{backgroundColor:'white'}">
        <div>选择难度</div>
        <select v-model="level" @change="update">
            <option value="default">默认</option>
            <option value="low">low</option>
            <option value="medium">medium</option>
            <option value="high">high</option>
            <option value="define">自定义</option>
        </select>
        <div>{{gameMsg }}</div>
        <div v-if="level === 'define'">
            <div>row:</div>
            <input type="number" v-model="m" @change="update">
            <div>col:</div>
            <input type="number" v-model="n" @change="update"> 
        </div>
    </div>
</template>

<script setup>
import { useStore } from 'vuex';
import { ref } from 'vue';
const store = useStore();
const m = ref(7);
const n = ref(7);
const gameMsg=ref("");
const level = ref("");
const levels= store.state.levels;
const update = () => {
    if (levels.has(level.value)){
        if (level.value==="default") {
            store.commit("setLevel",1);
            gameMsg.value="默认难度的迷宫生成成功";
            store.commit("update", levels.get(level.value));
        }else{
            store.commit("update", levels.get(level.value));
            gameMsg.value=`难度为${level.value}的迷宫生成成功`;
        }
    }else{
        if (m.value>5 && m.value<99 && (m.value&1) && n.value>5 && n.value<99 && (n.value&1)){
            store.commit("update", [m.value, n.value]);
            gameMsg.value=`${m.value}行${n.value}列的迷宫生成成功`;
        }else gameMsg.value="请输入在区间(5,99)内的奇数，你输入的数有误";
    }
}
</script>

<style scoped></style>
