<template>
  <div class="box" @keyup="herospin" @keydown="heromove" tabindex="0"> </div>
  <div class="winPage">
    <div class="PokeContainer">
      <div class="PokeItem" v-for="i in poke.length" :key="i">
        <img :src="images[poke[i - 1] + 4]" />
      </div>
    </div>
    <div class="Map">
      <div class="MapContainer">
        <div class="MapCol" v-for="j in gameRange(hero.posy.value - displayRange, hero.posy.value + displayRange, n)"
          :key="j">
          <div class="MapRow" v-for="i in gameRange(hero.posx.value - displayRange, hero.posx.value + displayRange, m)"
            :key="i">
            <img :src="imgbackground" />
          </div>
        </div>
      </div>
      <div class="MapContainer">
        <div class="MapCol" v-for="j in gameRange(hero.posy.value - displayRange, hero.posy.value + displayRange, n)"
          :key="j">
          <div class="MapRow" v-for="i in gameRange(hero.posx.value - displayRange, hero.posx.value + displayRange, m)"
            :key="i">
            <img v-if="j == hero.posy.value && i == hero.posx.value" :src="heroImage[hero.orientation.value]" />
            <img v-else :src="images[map[i - 1][j - 1]]" />
          </div>
        </div>
      </div>
    </div>
    <div class="tip">
      <div>经过时间:{{timer.minute}}:{{timer.second}}</div>
      <div>money:{{ money }}</div>
      <div>长x宽:{{[m,n]}}</div>
      <div>显示范围:{{displayRange}}</div>
      <div>log:{{ gameMsg }}</div>
    </div>
  </div>
  
</template>

<script setup>
import { useStore } from 'vuex';
import { ref, computed } from "vue";
import mazeCreator from '@/assets/mazeCreator.js';
import minralPutter from '@/assets/minralPutter.js';
import gacha from "@/assets/gacha.js";
const timer={
  minute:ref(0),
  second:ref(0),
  isActive:ref(true)
};
setInterval(() => {
  if (timer.isActive.value)
  {
    timer.second.value++;
    if (timer.second.value == 60) {
      timer.minute.value++;
      timer.second.value = 0;
    }
  }
}, 1000);
const store = useStore();
const m = computed(() => { return store.state.row });
const n = computed(() => { return store.state.col });
const nextFoot = ref(0);
const money = ref(0);
const poke = ref([]);
const displayRange = ref(5);
const gameMsg = ref("");
const map = ref(mazeCreator(m.value - 2, n.value - 2));
const endPos = ref(minralPutter(map.value, m.value, n.value));
const imgbackground = require("@/assets/BackGround.png");
const images = [
  require("@/assets/TransparentPicture.png"),
  require("@/assets/Stone.png"),
  require("@/assets/Exit.png"),
  require("@/assets/Ball0.png"),
  require("@/assets/Ball1.png"),
  require("@/assets/Diglett.png"),
  require("@/assets/Dialga.png"),
  require("@/assets/Meowth.png"),
  require("@/assets/Ampharos.png"),
  require("@/assets/Weezing.png")
];
const heroImage = [
  require("@/assets/Hero10.png"),
  require("@/assets/Hero11.png"),
  require("@/assets/Hero12.png"),
  require("@/assets/Hero20.png"),
  require("@/assets/Hero21.png"),
  require("@/assets/Hero22.png"),
  require("@/assets/Hero30.png"),
  require("@/assets/Hero31.png"),
  require("@/assets/Hero32.png"),
  require("@/assets/Hero40.png"),
  require("@/assets/Hero41.png"),
  require("@/assets/Hero42.png")
];
const keyCodeMap = new Map([
  [87, 0],
  [83, 3],
  [65, 6],
  [68, 9],
  [38, 0],
  [40, 3],
  [37, 6],
  [39, 9]
]);
const coordinateTransformation = new Map([
  [0, [-1, 0]],
  [3, [1, 0]],
  [6, [0, -1]],
  [9, [0, 1]]
]);
const hero = {
  orientation: ref(0),
  posx: ref(endPos.value[0][0]),
  posy: ref(endPos.value[0][1])
};
const mapBegin = {
  posx: ref(endPos.value[0][0]),
  posy: ref(endPos.value[0][1])
};
const mapExit = {
  posx: ref(endPos.value[1][0]),
  posy: ref(endPos.value[1][1])
};
const gameRange = (start, end, restriction) => {
  return Array(end - start + 1).fill().map((_, idx) => start + idx).filter(x => x >= 1 && x <= restriction)
}
const update = () => {
  store.commit("nextLevel");
  map.value = mazeCreator(m.value - 2, n.value - 2);
  endPos.value = minralPutter(map.value, m.value, n.value);
  hero.posx.value = endPos.value[0][0];
  hero.posy.value = endPos.value[0][1];
  hero.orientation.value = 0;
  mapBegin.value = { posx: endPos.value[0][0], posy: endPos.value[0][1] };
  mapExit.value = { posx: endPos.value[1][0], posy: endPos.value[1][1] };
};
const handleGacha = () => {
  money.value+=300;
  switch (gacha()) {
    case 0:
      break;
    case 1:
      hero.posx.value = endPos.value[0][0];
      hero.posy.value = endPos.value[0][1];
      gameMsg.value = "地鼠发动了挖洞技能,你回到了起点";
      poke.value.push(1);
      break;
    case 2:
      gameMsg.value = "迪亚鲁卡停滞了时间";
      timer.isActive.value=false;
      poke.value.push(2);
      break;
    case 3:
      money.value *= 2;
      gameMsg.value = "喵喵为你找来了更多金币";
      poke.value.push(3);
      break;
    case 4:
      poke.value.push(4);
      gameMsg.value = "电龙发动了闪光术，你能看到更大的范围";
      displayRange.value = Math.min(displayRange.value+1,7);
      break;
    case 5:
      poke.value.push(5);
      gameMsg.value ="你遭遇了双弹瓦斯，你的视野被遮蔽了";
      displayRange.value = Math.max(displayRange.value-1,3);
  }
};
const herospin = (event) => {
  if (keyCodeMap.has(event.keyCode))
  hero.orientation.value = keyCodeMap.get(event.keyCode);
}
const handleInteraction = (code) => {
  if (keyCodeMap.has(code)) {
    let tempOperator = keyCodeMap.get(code);
    if (coordinateTransformation.has(tempOperator)) {
      let tempCoordinateTransformation = coordinateTransformation.get(tempOperator);
      hero.orientation.value = tempOperator + 1 + nextFoot.value;
      switch (map.value[hero.posx.value + tempCoordinateTransformation[0] - 1][hero.posy.value + tempCoordinateTransformation[1] - 1]) {
        case 0:
          hero.posx.value += tempCoordinateTransformation[0];
          hero.posy.value += tempCoordinateTransformation[1];
          break;
        case 2:
          hero.posx.value += tempCoordinateTransformation[0];
          hero.posy.value += tempCoordinateTransformation[1];
          update();
          break;
        case 3:
          map.value[hero.posx.value + tempCoordinateTransformation[0] - 1][hero.posy.value + tempCoordinateTransformation[1] - 1] = 4;
          handleGacha();
          break;
      }
    }
  }
};
const heromove = (event) => {
  nextFoot.value = 1 - nextFoot.value;
  handleInteraction(event.keyCode);
};
</script>

<style scoped>
.tip{
  white-space: wrap;
  background-color: white;
  background-position: center center;
  padding: 20px;
}

.box {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  height: 200vh;
  width: 100%;
  overflow: hidden;
}

.MapCol {
  display: block;
}

.MapRow {
  height: 32px;
}

.PokeContainer {
  grid-template-columns: repeat(6, 75px);
  display: grid;
  justify-content: center;
}

.PokeItem {
  height: 75px;
}

.MapContainer {
  display: flex;
  justify-content: center;
  position: absolute;
  top:25%;
}

.Map {
  display: flex;
  justify-content: center;
  align-items: center;
}
.winPage {
  display: grid;
  grid-template-columns: 30% 50% 20%;
}</style>