"use strict";
export default function putter(map,col,row)  {
    let NOTHING = 0;
    let endPos=new Array();
    for (let i=1; i<col; i++)
        for (let j=1; j<row; j++)
            if (map[i][j]===NOTHING)
            {
                let degree=0;
                if (map[i-1][j]===NOTHING) degree++;
                if (map[i][j-1]===NOTHING) degree++;
                if (map[i+1][j]===NOTHING) degree++;
                if (map[i][j+1]===NOTHING) degree++;
                if (degree===1)
                {
                    
                    endPos.push([i+1,j+1]);
                }             
            }
    let pair=[endPos[0],endPos.pop()];
    endPos.shift();
    map[pair[1][0]-1][pair[1][1]-1]=2;
    for (let pair of endPos)
    {
        map[pair[0]-1][pair[1]-1]=3;
    }
    return pair
}