"use strict";
export default function mazecreator(row, col)  {
  let down = 1;
  let right = 2;
  let left = 4;
  let up = 8;
  let WALL = 1;
  let NOTHING = 0;
  class block {
    constructor(row, column, direction) {
      this.row = row;
      this.column = column;
      this.direction = direction;
    }
  }
  let myblock = new Array();
  let x_num = 1;
  let y_num = 1;
  let G = new Array(row + 2);
  for (let i = 0; i <G.length; i++) {
    G[i] = new Array(col+2).fill(WALL);
  }
  G[1][1] = NOTHING;
  function FindBlock() {
    //找出与当前位置相邻的墙
    if (x_num + 1 <= row && G[x_num + 1][y_num] == WALL) {
      //down
      myblock.push(new block(x_num + 1, y_num, down));
    }
    if (y_num + 1 <= col && G[x_num][y_num + 1] == WALL) {
      //right
      myblock.push(new block(x_num, y_num + 1, right));
    }
    if (x_num - 1 >= 1 && G[x_num - 1][y_num] == WALL) {
      //up
      myblock.push(new block(x_num - 1, y_num, up));
    }
    if (y_num - 1 >= 1 && G[x_num][y_num - 1] == WALL) {
      //left
      myblock.push(new block(x_num, y_num - 1, left));
    }
  }
  FindBlock();
  while (myblock.length!==0) {
    let BlockSize = myblock.length;
    let randnum = Math.floor(Math.random() * BlockSize);
    let SelectBlock = myblock[randnum];
    x_num = SelectBlock.row;
    y_num = SelectBlock.column;
    switch (SelectBlock.direction) {
      case down:
        x_num++;
        break;
      case right:
        y_num++;
        break;
      case left:
        y_num--;
        break;
      case up:
        x_num--;
        break;
    }
    if (G[x_num][y_num] == WALL) {
      G[SelectBlock.row][SelectBlock.column] = G[x_num][y_num] = NOTHING;
      FindBlock();
    } 
    myblock.splice(randnum, 1);
  }
  return G;
  /*
  for (let i = 0; i <= row+1; i++) {
    for (let j = 0; j <= col+1; j++) {
        process.stdout.write(String(G[i][j]));
    }
    process.stdout.write("\n");
  }*/
}





















